"use client";
import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { useAuth, useClerk } from "@clerk/nextjs";
import {
  Shield,
  Users,
  Smartphone,
  MapPin,
  BarChart3,
  Settings,
  FileText,
  Bell,
  LogOut,
  Search,
  AlertTriangle,
  CheckCircle,
  XCircle,
  Clock,
  Activity,
  TrendingUp,
  TrendingDown,
  Zap,
  ChevronRight,
} from "lucide-react";

export default function AdminDashboard() {
  const [searchQuery, setSearchQuery] = useState("");
  const router = useRouter();
  const { signOut } = useClerk();
  const { userId } = useAuth();

  const [currentPage, setCurrentPage] = useState("dashboard");

  // Mock Stats Data
  const stats = {
    totalEmployees: 124,
    activeEmployees: 98,
    totalDevices: 342,
    onlineDevices: 287,
    compliantDevices: 298,
    nonCompliantDevices: 44,
    criticalAlerts: 5,
    pendingApprovals: 12,
  };

  // Mock Department Data
  const departments = [
    { name: "Engineering", employees: 45, devices: 128, compliance: 94 },
    { name: "Sales", employees: 32, devices: 89, compliance: 87 },
    { name: "Marketing", employees: 21, devices: 58, compliance: 96 },
    { name: "HR", employees: 12, devices: 34, compliance: 100 },
    { name: "Operations", employees: 14, devices: 33, compliance: 91 },
  ];

  // Mock Recent Activity
  const recentActivity = [
    {
      id: 1,
      type: "device_enrolled",
      user: "Sarah Williams",
      device: "iPhone 15 Pro",
      time: "5 minutes ago",
      icon: CheckCircle,
      color: "text-green-600",
    },
    {
      id: 2,
      type: "compliance_failed",
      user: "Mike Johnson",
      device: "Android Phone",
      time: "12 minutes ago",
      icon: AlertTriangle,
      color: "text-red-600",
    },
    {
      id: 3,
      type: "device_synced",
      user: "John Doe",
      device: "MacBook Pro",
      time: "1 hour ago",
      icon: Activity,
      color: "text-blue-600",
    },
    {
      id: 4,
      type: "policy_updated",
      user: "Admin",
      device: "Screen Lock Policy",
      time: "2 hours ago",
      icon: Shield,
      color: "text-purple-600",
    },
    {
      id: 5,
      type: "device_removed",
      user: "Jane Smith",
      device: "iPad Air",
      time: "3 hours ago",
      icon: XCircle,
      color: "text-orange-600",
    },
  ];

  // Mock System Health Data
  const systemHealth = [
    { metric: "API Response Time", value: "124ms", status: "good", trend: "down" },
    { metric: "Database Load", value: "42%", status: "good", trend: "up" },
    { metric: "Active Connections", value: "287", status: "good", trend: "up" },
    { metric: "Error Rate", value: "0.12%", status: "good", trend: "down" },
  ];

  // ✅ Navigation for pages
  const handleNavigation = (page) => {
    setCurrentPage(page);
    switch (page) {
      case "dashboard":
        router.push("/admin"); // current page
        break;
      case "employees":
        router.push("/admin/employees");
        break;
      case "devices":
        router.push("/admin/devices");
        break;
      case "geolocation":
        router.push("/admin/geolocation");
        break;
      case "compliance":
        router.push("/admin/compliance");
        break;
      case "policies":
        router.push("/admin/policies");
        break;
      case "reports":
        router.push("/admin/reports");
        break;
      case "settings":
        router.push("/admin/settings");
        break;
      case "activity":
        router.push("/admin/activity");
        break;
      default:
        router.push("/admin");
    }
  };

  // ✅ Logout Function (Clerk)
  const handleLogout = async () => {
    try {
      await signOut();
      router.push("/login"); // Redirect to login after logout
    } catch (err) {
      console.error("Logout failed:", err);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Admin Navbar */}
      <nav className="bg-white border-b border-gray-200 sticky top-0 z-50 shadow-sm">
        <div className="px-6 py-4">
          <div className="flex items-center justify-between">
            {/* Logo */}
            <div className="flex items-center gap-3">
              <div className="p-2 bg-gradient-to-br from-blue-600 to-purple-600 rounded-lg shadow-lg">
                <Shield className="w-6 h-6 text-white" />
              </div>
              <div>
                <h1 className="text-xl font-bold text-gray-900">BYOD Admin Portal</h1>
                <p className="text-xs text-gray-500">System Management Dashboard</p>
              </div>
            </div>

            {/* Search */}
            <div className="flex-1 max-w-2xl mx-8">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-gray-400" />
                <input
                  type="text"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  placeholder="Search employees, devices, policies..."
                  className="w-full pl-10 pr-4 py-2.5 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                />
              </div>
            </div>

            {/* Profile & Logout */}
            <div className="flex items-center gap-4">
              <button className="relative p-2 text-gray-600 hover:bg-gray-100 rounded-lg transition">
                <Bell className="w-6 h-6" />
                <span className="absolute top-1 right-1 w-5 h-5 bg-red-500 text-white text-xs rounded-full flex items-center justify-center font-semibold">
                  5
                </span>
              </button>

              <div className="flex items-center gap-3 pl-4 border-l border-gray-200">
                <div className="text-right">
                  <p className="text-sm font-semibold text-gray-900">Admin User</p>
                  <p className="text-xs text-gray-500">System Administrator</p>
                </div>
                <div className="w-10 h-10 bg-gradient-to-br from-yellow-500 to-orange-600 rounded-full flex items-center justify-center text-white font-semibold shadow-lg">
                  AD
                </div>
                <button
                  onClick={handleLogout}
                  className="p-2 text-gray-600 hover:bg-red-50 hover:text-red-600 rounded-lg transition"
                  title="Logout"
                >
                  <LogOut className="w-5 h-5" />
                </button>
              </div>
            </div>
          </div>
        </div>
      </nav>

      <div className="flex">
        {/* Sidebar */}
        <aside className="w-64 bg-white border-r border-gray-200 min-h-screen sticky top-16">
          <nav className="p-4 space-y-2">
            <button
              onClick={() => handleNavigation("dashboard")}
              className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg font-medium transition ${
                currentPage === "dashboard"
                  ? "bg-blue-50 text-blue-600"
                  : "text-gray-700 hover:bg-gray-50"
              }`}
            >
              <BarChart3 className="w-5 h-5" />
              Dashboard
            </button>
            <button
              onClick={() => handleNavigation("employees")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <Users className="w-5 h-5" />
              Employees
            </button>
            <button
              onClick={() => handleNavigation("devices")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <Smartphone className="w-5 h-5" />
              Devices
            </button>
            <button
              onClick={() => handleNavigation("geolocation")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <MapPin className="w-5 h-5" />
              Geolocation
            </button>
            <button
              onClick={() => handleNavigation("compliance")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <Shield className="w-5 h-5" />
              Compliance
            </button>
            <button
              onClick={() => handleNavigation("policies")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <FileText className="w-5 h-5" />
              Policies
            </button>
            <button
              onClick={() => handleNavigation("reports")}
              className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
            >
              <Activity className="w-5 h-5" />
              Reports
            </button>

            <div className="pt-4 mt-4 border-t border-gray-200">
              <button
                onClick={() => handleNavigation("settings")}
                className="w-full flex items-center gap-3 px-4 py-3 text-gray-700 hover:bg-gray-50 rounded-lg font-medium transition"
              >
                <Settings className="w-5 h-5" />
                Settings
              </button>
            </div>
          </nav>
        </aside>

        {/* Main Dashboard Content */}
        <main className="flex-1 p-6 space-y-6">
          {/* Header */}
          <div className="flex items-center justify-between">
            <div>
              <h2 className="text-3xl font-bold text-gray-900">Dashboard Overview</h2>
              <p className="text-gray-600 mt-1">
                Monitor and manage your BYOD ecosystem
              </p>
            </div>
            <div className="flex items-center gap-2 text-sm text-gray-600">
              <Clock className="w-4 h-4" />
              Last updated: {new Date().toLocaleTimeString()}
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}